  components: {
    banner,
    payBox,
    selectTab
  },
  filters: {
     getFormatDate (date: any) {
      return new Date(date).getFullYear() + '' + (new Date(date).getMonth() + 1) + '' + new Date(date).getDate() + ''
    },
    getFoodNum (food) {
      return (food.num && food.num > 0) ? food.num : 0
    }
  }
})
